import { reactive, computed, watch } from "vue";
import { useStore } from "vuex";
export default {
  name: "card_content",
  props: {
    msg: String
  },

  setup() {
    const store = useStore();
    let RequestUrl = store.state.RequestUrl; //用计算属性取到vuex中的值

    const getShowTask = computed(() => {
      return store.state.RequestUrl;
    }); //监视vux中请求地址的变化

    watch(getShowTask, (newVal, oldVal) => {
      RequestUrl = newVal;
      console.log("RequestUrl变化了");
    });
    let SendingAddress = "";
    let InputValue = reactive({
      Name: "",
      Phone: "",
      QQ: "",
      Email: "",
      Company: "",
      TIL: "",
      Position: "",
      Website: "",
      Address: ""
    }); //点击生成二维码事件

    function become(values) {
      SendingAddress = RequestUrl + `MECARD:TEL:${values.Phone};URL:${values.Website};EMAIL:${values.Email};NOTE:${values.QQ};N:${values.Name};ORG:${values.Company};TIL:${values.TIL};ADR:${values.Address};`; // 触发vuex中的actions，用于异步修改state的信息

      store.dispatch("upSendingAddress", SendingAddress);
    }

    return {
      InputValue,
      become
    };
  }

};